.shots {
  padding: 80px 0 !important;
  > .section-title {
    z-index: 100;
    position: relative;
    > * {
      color: #fff;
    }
  }
  .shots-slider {
    $height: 580px;
    $plusHeight: 32px;
    .owl-stage {
      max-height: calc($height + $plusHeight);
      overflow: hidden;
    }
    .item {
      img {
        height: $height;
        margin-bottom: 0.5em;
        border-radius: 10px;
      }
      p {
        font-family: $secondary-font;
        font-style: italic;
        overflow: hidden;
        text-overflow: ellipsis;
        color: #fff;
        opacity: 0.5;
      }
    }
    @media (width < 600px) {
      $height: 500px;
      .owl-stage {
        max-height: calc($height + $plusHeight);
      }
      .item img {
        height: $height;
      }
    }
    @media (width < 500px) {
      $height: 400px;
      .owl-stage {
        max-height: calc($height + $plusHeight);
      }
      .item img {
        height: $height;
      }
      .item p {
        display: none;
      }
    }
    @media (width < 400px) {
      $height: 300px;
      .owl-stage {
        max-height: calc($height + $plusHeight);
      }
      .item img {
        height: $height;
      }
    }
    @media (width < 300px) {
      $height: 200px;
      .owl-stage {
        max-height: calc($height + $plusHeight);
      }
      .item img {
        height: $height;
      }
    }

    // Owl dot Active color overide
    .owl-dots {
      .owl-dot {
        &:hover {
          span {
            background: $primary-color;
          }
        }

        &.active {
          span {
            background: $primary-color;
          }
        }
      }
    }
  }
}

/* homepage 3 */
.gradient-banner {
  padding: 100px 0 170px;
  position: relative;
  overflow: hidden;
  &::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 200%;
    height: 200%;
    // border-radius: 50%;
    background-image: linear-gradient(
      45deg,
      #009ec5 0%,
      $primary-color 20%,
      #02225b 50%
    );
  }
}

.pull-top {
  margin-top: -100px;
}

.shapes-container {
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  .shape {
    position: absolute;

    &::before {
      content: "";
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(255, 255, 255, 0.1);
      transform: rotate(-35deg);
      position: absolute;
      border-radius: 50px;
    }
  }

